@use '../util' as *;

.about {
    display: flex;
    align-items: center;
    justify-content: space-between;

    @include breakpoint-down(large) {
        flex-direction: column;
        align-items: center;

        &:nth-child(2) {
            flex-direction: column-reverse;
        }

        &__promo {
            margin-bottom: 2.375rem
        }
    }

    &:nth-child(2) {
        padding-top: rem(35);

        .title::before {
            content: 'About us';
        }
    }

    &:nth-child(5) .title {
        max-width: rem(600);

        &::before {
            content: 'Next Tour';
        }
    }

    &__content {
        max-width: rem(740);
        padding: 0 rem(30);

        .btn {
            margin-top: rem(18);
        }
    }
}