:root {
  --font-body: "Montserrat", sans-serif;
  --transition: .2s;
}

:root {
  --text: hsl(240, 1%, 50%);
  --button: hsl(212, 18%, 50%);
  --white: hsl(0, 0%, 100%);
  --light-grey: hsl(0, 1%, 85%);
  --dark-grey: hsl(0, 0%, 50%);
  --black: hsl(0, 0%, 0%);
}

html {
  font-size: 100%;
  box-sizing: border-box;
  scroll-behavior: smooth;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-size: 1.375rem;
  font-family: var(--font-body);
}

img {
  max-width: 100%;
}

.title {
  letter-spacing: 0.25rem;
  text-transform: uppercase;
  margin-bottom: 2.375rem;
  font-size: 1.875rem;
  font-weight: 500;
  position: relative;
}
.title::before {
  content: "";
  text-transform: capitalize;
  font-size: 1.375rem;
  color: var(--dark-grey);
  position: absolute;
  top: -2.1875rem;
}
.title--small {
  margin: 3.75rem 0 2.8125rem;
  font-size: 1.125rem;
}
.title--big {
  margin-bottom: 1.1875rem;
  font-weight: 700;
}

.subtitle {
  margin-bottom: 5rem;
  font-size: 1.75rem;
  font-weight: 500;
  color: var(--text);
}

.text {
  margin-bottom: 2.75rem;
  color: var(--text);
}
.text--big {
  margin-bottom: 5rem;
  line-height: 2.5rem;
  max-width: 43.75rem;
}

.input {
  font-size: 1.25rem;
  background-color: transparent;
  border: 0.125rem solid var(--dark-grey);
  padding: 1.25rem 0 1.25rem 2.5rem;
}

.btn {
  font-size: 1.25rem;
  padding: 0.9375rem 5.625rem;
  text-transform: uppercase;
  transition: var(--transition);
  border: 0.125rem solid var(--black);
}
.btn:hover {
  background-color: var(--black);
  color: var(--white);
}

:root {
  --swiper-theme-color: var(--dark-grey);
  --swiper-navigation-size: 34px;
}

h1,
h2,
h3 {
  margin-top: 0;
  line-height: 1.1;
}

ul {
  margin: 0;
  padding: 0;
}

li {
  display: inline-block;
  list-style-type: none;
}

a,
a:visited,
a:active {
  display: inline-block;
  text-decoration: none;
  color: var(--black);
}

section:not(.footer) {
  padding-top: 9.375rem;
}

.container {
  padding: 0 1.5rem;
  max-width: 107.5rem;
  margin: 0 auto;
}
@media (min-width: 71.875em) {
  .container {
    transform: translate(0, 0);
    padding: 0;
  }
}

.header {
  position: relative;
  background: var(--black) url("../images/header.webp") no-repeat 50%/cover fixed;
}
.header::after {
  content: "";
  min-width: 100%;
  min-height: 100%;
  background-color: rgba(0, 0, 0, 0.35);
  position: absolute;
  top: 0;
}
.header__inner {
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  position: relative;
  min-height: 100vh;
  z-index: 1;
}
.header__inner, .header__inner a {
  color: var(--white);
}
.header__menu {
  position: absolute;
  top: 2.3125rem;
  left: 50%;
  font-size: 0;
  transform: translateX(-50%);
}
@media (max-width: 71.8125em) {
  .header__menu {
    left: 25%;
  }
}
.header__menu * {
  font-size: 1.25rem;
  display: block;
}
@media (min-width: 71.875em) {
  .header__menu * {
    display: inline-block;
  }
  .header__menu *:not(:last-child) {
    margin-right: 7rem;
  }
}
.header__menu *:hover {
  text-decoration: underline;
}
.header__menu--aside {
  left: 75%;
  transform: translateX(-50%) rotate(0deg);
}
@media (min-width: 71.875em) {
  .header__menu--aside {
    top: 50%;
    left: 2.3125rem;
    transform: translate(-50%, -50%) rotate(270deg);
  }
}
.header__title {
  font-weight: 700;
  font-size: 6.25rem;
  line-height: 0.1;
}
@media (max-width: 87.4375em) {
  .header__title {
    margin-bottom: 1.25rem;
    line-height: initial;
    font-size: 3.75rem;
  }
}
.header__subtitle {
  font-size: 1.875rem;
  margin-bottom: 4.0625rem;
}
.header__btn {
  border-color: var(--white);
}
.header__btn:hover {
  background-color: rgba(0, 0, 0, 0.35);
}

.about {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
@media (max-width: 87.4375em) {
  .about {
    flex-direction: column;
    align-items: center;
  }
  .about:nth-child(2) {
    flex-direction: column-reverse;
  }
  .about__promo {
    margin-bottom: 2.375rem;
  }
}
.about:nth-child(2) {
  padding-top: 2.1875rem;
}
.about:nth-child(2) .title::before {
  content: "About us";
}
.about:nth-child(5) .title {
  max-width: 37.5rem;
}
.about:nth-child(5) .title::before {
  content: "Next Tour";
}
.about__content {
  max-width: 46.25rem;
  padding: 0 1.875rem;
}
.about__content .btn {
  margin-top: 1.125rem;
}

body .swiper {
  min-height: 88.5rem;
}
@media (max-width: 87.4375em) {
  body .swiper-slide img {
    min-width: 100%;
  }
}
body .swiper__text {
  max-width: 53.75rem;
  padding: 0 1.25rem;
  text-align: center;
  margin: 0 auto;
}
body .swiper-wrapper {
  margin-bottom: 9.6875rem;
}
body .swiper-content {
  display: flex;
  padding: 0 1.5rem;
  justify-content: space-between;
}
body .swiper-content:not(:last-child) {
  margin-bottom: 2.5rem;
}
@media (max-width: 87.4375em) {
  body .swiper-content img:not(:first-child) {
    display: none;
  }
}
body .swiper-content img:not(:last-child) {
  margin-right: 1.875rem;
}
body .swiper-pagination {
  color: hsl(0deg, 0%, 77%);
  letter-spacing: 0.2625rem;
  font-size: 1.875rem;
}
body .swiper-pagination-total {
  color: var(--black);
}
body .swiper-buttons {
  display: flex;
  justify-content: center;
}
body .swiper-button-prev, body .swiper-button-next {
  position: relative;
  font-weight: 700;
  z-index: 11;
  bottom: 0.75rem;
  margin: 0 6.125rem;
}

.travel {
  padding: 0 1.5rem;
  font-size: 1.125rem;
  letter-spacing: 0.2375rem;
}
.travel__items {
  display: flex;
  justify-content: space-between;
}
@media (max-width: 87.4375em) {
  .travel__items {
    flex-direction: column;
    align-items: center;
  }
}
.travel__content:not(:last-child) {
  margin-right: 1.875rem;
}
@media (max-width: 87.4375em) {
  .travel__content:not(:last-child) {
    margin: 0 0 5rem;
  }
}
.travel__text {
  max-width: 65.625rem;
  padding: 0 1.25rem;
  text-align: center;
  margin: 0 auto;
}
.travel__btn {
  font-size: 1rem;
  padding: 1.125rem 2.8125rem;
  border-color: var(--light-grey);
}
.travel__btn, .travel__btn:hover {
  background-color: var(--light-grey);
  color: var(--black);
}

.tourist {
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: var(--light-grey);
  margin-top: 5.625rem;
  padding: 9.375rem;
}
@media (max-width: 87.4375em) {
  .tourist {
    flex-direction: column;
    text-align: center;
  }
}
.tourist__form {
  font-size: 0;
}
.tourist__form *:not(:last-child) {
  margin-bottom: 1.25rem;
}
@media (min-width: 71.875em) {
  .tourist__form * {
    margin-left: 3.75rem;
  }
}
.tourist__subtitle {
  font-size: 1.125rem;
  color: var(--text);
  margin-top: -1.75rem;
}
.tourist__btn {
  font-size: 1rem;
  padding: 1.375rem 5.625rem;
  border-color: var(--button);
  color: var(--white) !important;
}
.tourist__btn, .tourist__btn:hover {
  background-color: var(--button);
}

.details {
  text-align: center;
  text-transform: uppercase;
  padding: 5.3125rem 0 6.5625rem;
}
.details__menu {
  font-size: 0;
  margin-bottom: 3.4375rem;
}
.details__menu li + li {
  margin-left: 4rem;
}
@media (max-width: 87.4375em) {
  .details__menu li + li {
    margin-top: 1.25rem;
    margin-left: 0;
    display: block;
  }
}
.details__menu a {
  font-size: 1.375rem;
}
.details__menu a:hover {
  text-decoration: underline;
}
.details__copyright {
  font-size: 1.125rem;
}
.details__copyright p {
  margin: 0;
}
.details__copyright p:not(:last-child) {
  margin-bottom: 0.625rem;
}